html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  background-color: #f8f8f8;
  /*     &.body-scroll{
          padding-top: 8.6rem;
      } */
}
body.fix-body {
  overflow: hidden;
}

p {
  color: #000000;
  font-size: 1.6rem;
  line-height: 2;
}

a {
  text-decoration: none;
  color: #000000;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.container {
  width: min(90%, 120rem);
  margin: 0 auto;
}

[class$=container],
.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.section {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .section {
    padding: 8rem 0;
  }
}

.header {
  padding-top: 5rem;
}
.header__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__grid {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  max-width: 20rem;
}
.header__logo img {
  width: 100%;
}

.navbar {
  background-color: #8ed3cc;
  padding: 0.2rem 0;
  margin: 2rem 0;
}
.navbar__border {
  border-block: 2px dashed #FFFFFF;
  padding: 1.7rem 0;
}
.navbar__link {
  color: #FFFFFF;
  font-size: 2.4rem;
  margin-bottom: 1.2rem;
  transition: 0.3s;
}
.navbar__link:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .navbar__link {
    margin-bottom: 0;
  }
}
.navbar__link:hover {
  opacity: 0.5;
}
.navbar__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .navbar__container {
    flex-direction: row;
    justify-content: space-evenly;
  }
}

.slider {
  overflow: hidden;
  position: relative;
}
.slider__slide {
  height: 100%;
  flex-shrink: 0;
  width: 100%;
  position: relative;
  transition-property: transform;
}
.slider__img {
  width: 100%;
}
.slider .swiper-button-next,
.slider .swiper-button-prev {
  color: #FFFFFF;
}
.slider .swiper-pagination-bullet {
  background-color: #FFFFFF;
}
.slider .swiper-pagination-bullet-active {
  background-color: #8ed3cc;
}

.swiper-slide {
  background-position: center;
  background-size: cover;
}

.swiper-slide img {
  display: block;
  width: 100%;
}

.welcome {
  text-align: center;
}
.welcome__title {
  text-align: center;
  color: #e082a8;
  font-family: "Italianno", sans-serif;
  line-height: 0.8;
  font-weight: 400;
  text-transform: lowercase;
  font-size: 4rem;
}
.welcome__title span {
  color: #000000;
  display: block;
  text-transform: uppercase;
  font-family: "Lato", sans-serif;
}
.welcome__text {
  font-size: 2.6rem;
  color: #979797;
  max-width: 50rem;
  margin: 2rem auto 0rem auto;
  line-height: 1.8;
}

.cards {
  display: grid;
}
@media (min-width: 768px) {
  .cards__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.card {
  max-width: 35rem;
  margin: 0 auto 2rem auto;
}
.card:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .card {
    max-width: unset;
    margin: 0;
  }
}
.card__img {
  height: 30rem;
  width: 100%;
  border: 0.5rem solid #FFFFFF;
  transition: 0.3s;
}
.card__img:hover {
  transform: rotate(4deg) scale(1.1);
}
.card__content {
  max-width: 90%;
  margin: 0 auto;
  background-color: #FFFFFF;
  transform: translateY(-3rem);
  padding: 1.5rem 1rem;
}
.card__title {
  text-align: center;
  color: #e082a8;
  font-family: "Italianno", sans-serif;
  line-height: 0.8;
  font-weight: 400;
  text-transform: lowercase;
  font-size: 4rem;
  padding: 2rem;
  font-size: 3.5rem;
}
.card__title span {
  color: #000000;
  display: block;
  text-transform: uppercase;
  font-family: "Lato", sans-serif;
}
.card__title span {
  font-size: 3rem;
}
.card__link {
  background-color: #e082a8;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: 0.3s ease;
  border: none;
  border-radius: unset;
  display: block;
}
.card__link:hover {
  cursor: pointer;
  opacity: 0.5;
  color: gray;
}
@media (min-width: 768px) {
  .card__link {
    width: auto;
    display: inline-block;
  }
}

.schedule {
  background-image: linear-gradient(to bottom, #8ed3cc 0%, #8ed3cc 50%, transparent 50%), url(../img/bgDate.jpg);
  background-repeat: no-repeat;
  background-position: center, 50%;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .schedule {
    background-image: linear-gradient(to right, #8ed3cc 0%, #8ed3cc 50%, transparent 50%), url(../img/bgDate.jpg);
    background-position: center, center 70%;
    background-size: cover;
  }
}
.schedule__grid {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .schedule__grid {
    grid-template-rows: unset;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.schedule__content {
  padding: 5rem 0;
}
.schedule__title {
  text-transform: uppercase;
  text-align: center;
  font-weight: 400;
  font-size: 4rem;
  font-size: 3rem;
}
.schedule__text {
  color: #FFFFFF;
  font-size: 2.2rem;
  line-height: 1.8;
  text-align: center;
}

.table {
  width: 100%;
  border: 0.5rem solid #e082a8;
  text-align: center;
  border-collapse: collapse;
}
.table__head {
  background-color: #e082a8;
}
.table__th {
  padding: 1rem;
}
.table__tr:hover {
  background-color: #69c5bb;
}
.table__tr--sunday {
  background-color: #e082a8;
}
.table__td {
  padding: 1rem;
  font-size: 2.2rem;
}

.products__title {
  text-align: center;
  color: #e082a8;
  font-family: "Italianno", sans-serif;
  line-height: 0.8;
  font-weight: 400;
  text-transform: lowercase;
  font-size: 4rem;
  margin-bottom: 5rem;
}
.products__title span {
  color: #000000;
  display: block;
  text-transform: uppercase;
  font-family: "Lato", sans-serif;
}
.products__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .products__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .products__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}

.product__content {
  background-color: #FFFFFF;
  padding: 2rem;
}
.product__mask {
  overflow: hidden;
}
.product__img {
  margin: 0 auto 2rem auto;
  height: 30rem;
  width: 30rem;
  transition: 0.3s;
}
.product__img:hover {
  transform: scale(1.1);
}
.product__title {
  text-transform: uppercase;
  text-align: center;
  font-weight: 400;
  font-size: 4rem;
  font-size: 2.4rem;
  color: #e082a8;
}
.product__text {
  font-size: 1.6rem;
  text-transform: uppercase;
  line-height: 1.8;
}
.product__price {
  text-align: center;
  color: #979797;
  font-size: 2.8rem;
  margin: 0;
}

.date {
  background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url(../img/bgDate.jpg);
  background-position: 30% 70%;
  padding: 10rem;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  text-align: center;
}
.date__title {
  text-transform: uppercase;
  text-align: center;
  font-weight: 400;
  font-size: 4rem;
  color: #FFFFFF;
  font-size: 6rem;
}
.date__text {
  max-width: 60rem;
  color: #FFFFFF;
  margin: 0 auto;
  line-height: 2;
}
.date__link {
  background-color: #8ed3cc;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: 0.3s ease;
  border: none;
}
.date__link:hover {
  cursor: pointer;
  opacity: 0.5;
  color: gray;
}
@media (min-width: 768px) {
  .date__link {
    width: auto;
    display: inline-block;
  }
}

.socials {
  margin: 5rem 0;
  display: flex;
}
@media (min-width: 768px) {
  .socials {
    margin: 0;
  }
}
.socials__link {
  transition: 0.3s;
}
.socials__link::before {
  content: "";
  padding: 2rem;
  display: inline-block;
}
.socials__link[href*="facebook.com"]::before {
  background-image: url(../img/icono_facebook.svg);
}
.socials__link[href*="twitter.com"]::before {
  background-image: url(../img/icono_twitter.svg);
}
.socials__link[href*="instagram.com"]::before {
  background-image: url(../img/icono_instagram.svg);
}
.socials__link[href*="youtube.com"]::before {
  background-image: url(../img/icono_youtube.svg);
}
.socials__link[href*="pinterest.com"]::before {
  background-image: url(../img/icono_pinterest.svg);
}
.socials__link[href*="tiktok.com"]::before {
  background-image: url(../img/icono_tiktok.svg);
}
.socials__link:hover {
  opacity: 0.6;
}
.socials__accessible {
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
  display: block;
}

.footer {
  padding-top: 5rem;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.footer__widget {
  text-align: center;
}
.footer__widget:nth-child(3) {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.footer__title {
  text-transform: uppercase;
  text-align: center;
  font-weight: 400;
  font-size: 4rem;
  font-size: 3.4rem;
}
.footer__text {
  line-height: 1.8;
}
.footer__copyright {
  text-align: center;
  max-width: 100%;
  margin: 2 auto 0 auto;
  border-top: 1px solid #7e7e7e;
  font-size: 1.6rem;
  padding: 2rem;
  color: #979797;
}