@use 'base/mixins' as m;
@use 'base/variables' as v;

.navbar{
    background-color: v.$primary;
    padding: .2rem 0;
    margin: 2rem 0;
    &__border{
        border-block:2px dashed v.$white; 
        padding: 1.7rem 0;
    }
    &__link{
        color: v.$white;
        font-size: 2.4rem;
        margin-bottom: 1.2rem;
        transition: 0.3s;
        &:last-of-type{
            margin-bottom: 0;
        }
        @include m.tablet {
            margin-bottom: 0;
        }
        &:hover{
            opacity: 0.5;
        }
    }
    &__container{
        display: flex;
        flex-direction: column;
        align-items: center;
        @include m.tablet {
            flex-direction: row;
            justify-content: space-evenly;
        }
    }
}