@use 'base/mixins' as m;
@use 'base/variables' as v;

.date{
    background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url(../img/bgDate.jpg);
    background-position: 30% 70%;
    padding: 10rem;
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    text-align: center;
    

    &__title{
        @include m.heading;
        color: v.$white;
        font-size: 6rem;
    }
    &__text{
        max-width: 60rem;
        color: v.$white;
        margin: 0 auto;
        line-height: 2;
    }
    &__link{
        @include m.btn(v.$primary, v.$white ) ;
        
    }
}