@use 'base/mixins' as m;
@use 'base/variables' as v;

.cards{
    display: grid;
    &__grid{
        @include m.tablet {
            @include m.grid(3, 2rem ) ;
        }
    }
}

.card{
    max-width: 35rem;
    margin: 0 auto 2rem auto;

    &:last-of-type{
        margin-bottom: 0;
    }

    @include m.tablet {
        max-width: unset;
        margin: 0;
    }

    &__img{
        height: 30rem;
        width: 100%;
        border: .5rem solid v.$white;
        transition: 0.3s;

        &:hover{
            transform: rotate(4deg) scale(1.1);
        }
    }
    &__content{
        max-width: 90%;
        margin: 0 auto;
        background-color: v.$white;
        transform: translateY(-3rem);
        padding: 1.5rem 1rem;
    }

    &__title{
        @include m.headingCursiva;
        padding: 2rem;
        font-size: 3.5rem;
        span{
            font-size: 3rem;
        }
     
    }

    &__link{
        @include m.btn(v.$secondary, v.$white ) ;
        border-radius: unset;
        display: block;
    }

}