@use 'base/mixins' as m;
@use 'base/variables' as v;

.schedule{
    background-image: linear-gradient(to bottom, v.$primary 0% ,v.$primary 50% , transparent 50% ), url(../img/bgDate.jpg);
    background-repeat: no-repeat;
    background-position: center, 50%;
    color: v.$white;
    @include m.tablet {
        background-image: linear-gradient(to right, v.$primary 0% ,v.$primary 50% , transparent 50% ), url(../img/bgDate.jpg);
        background-position: center, center 70%;
        background-size: cover;
    }

    &__grid{
        display: grid;
        grid-template-rows: repeat(2, 1fr);
        @include m.tablet {
            grid-template-rows: unset;
            @include m.grid(2,5rem ) ;
        }
    }
    &__content{
        padding: 5rem 0;
    }
    &__title{
        @include m.heading;
        font-size: 3rem;
    }
    &__text{
        color: v.$white;
        font-size: 2.2rem;
        line-height: 1.8;
        text-align: center;
    }

}

.table{
    width: 100%;
    border: .5rem solid v.$secondary;
    text-align: center;
    border-collapse: collapse;

    &__head{
        background-color: v.$secondary;
    }
    &__th{
        padding:1rem ;
    }

    &__tr{
        &:hover{
            background-color: darken(v.$primary, 10%);
        }
        &--sunday{
            background-color: v.$secondary;
        }
    }
    &__td{
        padding: 1rem;
        font-size: 2.2rem;
    }
}