@use 'base/mixins' as m;
@use 'base/variables' as v;

.products{
    &__title{
        @include m.headingCursiva;
        margin-bottom: 5rem;
    }
    &__grid{
        @include m.grid(1, 2rem) ;
        @include m.tablet {
            @include m.grid(2, 2rem) ;
        }
        @include m.desktop {
            @include m.grid(4, 2rem) ;

        }

    }
}

.product{
    &__content{
        background-color: v.$white;
        padding: 2rem;
    }
    &__mask{
        overflow: hidden;
    }
    &__img{
        margin: 0 auto 2rem auto;
        height: 30rem;
        width: 30rem;
        transition: 0.3s;
        &:hover{
            transform: scale(1.1)
        }
    }
    &__title{
        @include m.heading;
        font-size: 2.4rem;
        color: v.$secondary;
    }
    &__text{
        font-size: 1.6rem;
        text-transform: uppercase;
        line-height: 1.8;
    }
    &__price{
        text-align: center;
        color: v.$gray;
        font-size: 2.8rem;
        margin: 0;
    }
}
 
